.chat{
    width: 370px;
    height: 490px;
    bottom: 10px;
    right: 10px;
    border-radius: 10px;
    overflow: hidden;
  border: 1px solid #ddd;
    background-color: white;
    

}
.container-chat{
    height: 470px;
    width: 370px;
    float: left;
   
    bottom: 150px;
    right: 50px;
    position: fixed;
    z-index: 999999;
}
.pointer{
    cursor: pointer;
}
.btn-chat{
    padding: 8px;
    
    border-radius: 100%;
        margin-top: 10px;
        cursor: pointer;
        position: fixed;
        bottom: 50px;
        right: 50px;
        border: 1px solid white;
}
.header-chat{
    height: 50px;
   
}
.message-chat{
  flex: 1;
    background-color: white;
    overflow-y: scroll;
    
}
.chat-footer{
    background-color: #f2f2f2;
    padding: 10px;
}